.bg-main {
  background-color: #fc9842;
  background: -webkit-linear-gradient(315deg, #fc9842 0%, #fe5f75 74%) no-repeat center center fixed;
  background: -o-linear-gradient(315deg, #fc9842 0%, #fe5f75 74%) no-repeat center center fixed;
  background: -moz-linear-gradient(315deg, #fc9842 0%, #fe5f75 74%) no-repeat center center fixed;
  background: linear-gradient(315deg, #fc9842 0%, #fe5f75 74%) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

