.h-450 {
  height: 450px;
}

.border-right-orange {
  border-right: 1px solid $salmon;
}

.border-left-orange {
  border-left: 1px solid $salmon;
}

.border-orange {
  border: 1px solid $salmon;
}

.border-ruby-red {
  border: 1px solid $ruby-red;
}

.border-right-ruby-red {
  border-left: 1px solid $ruby-red;
}

.border-left-ruby-red {
  border-right: 1px solid $ruby-red;
}

.border-light-red {
  border: 1px solid $light-red;
}

.border-right-light-red {
  border-left: 1px solid $light-red;
}

.border-left-light-red {
  border-right: 1px solid $light-red;
}

.border-dark-blue {
  border: 1px solid $dark-blue;
}

.border-cyan {
  border: 1px solid $cyan;
}

.border-right-cyan {
  border-left: 1px solid $cyan;
}

.border-left-cyan {
  border-right: 1px solid $cyan;
}

.border-light-blue {
  border: 1px solid $light-blue;
}

.border-right-light-blue {
  border-left: 1px solid $light-blue;
}

.border-left-light-blue {
  border-right: 1px solid $light-blue;
}

.tag-green {
  background-color: $green-low;
  padding: 5px;
  border-radius: 15px;
  color: $white;
}

.no-bullets {
  list-style-type: none;
}
